import {POST,GET} from './request.js'   //
/**
 * 
 * @param {*} type  0get 1post
 * @param {*} url   
 * @param {*} data 
 */
const requestData = async (type,url,data)=>{
    let res
    if(type == 1) res = await GET(url,data)
    if(type == 3) res = await GET(url,data,"/easyapi/doc/auth")
    if(type == 4) res = await  POST(url,data,"/easyapi/doc/auth")
    if(type == 2 ||!type) res = await POST(url,data)

    return res
}


export {requestData}